<template>
    <div>
        <!-- 登录部分 -->
        <div class="login">
           <div class="background">
                <svg>
                <defs>
                    <filter id="background" x="0" y="0">
                        <feImage xlink:href="https://ac.gtimg.com/media/images/ac_logo.png"  x="0" y="0" preserveAspectRatio="xMidYMid slice"></feImage>
                         <feGaussianBlur stdDeviation="30"></feGaussianBlur>
                    </filter>
                </defs>
                <rect x="0" y="0" filter="url(#background)">
                </rect>
            </svg>
           </div>
            <div class="content">
                <div class="pic">
                <h1>炫火漫</h1>
                </div>
               <div class="data">
                   <div class="account">
                        账号：<input type="text" placeholder="请输入账号" v-model="account">
                        <span>{{$store.state.user.login_input_error}}</span>
                   </div>
                   <div class="password">
                        密码：<input type="password" placeholder="请输入密码" v-model="password" >
                   </div>
               </div>
               <van-button color="#ff5127" @click="$store.commit('user/login',{account,password})">登录</van-button>
               <a href="#" @click="register">没有账号？请注册</a>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            account:"admin",
            password:"123456"
        }
    },
    watch:{
      "$store.state.user.islogin":function(newvalue){
            if(newvalue==true){
               
                    this.$router.push({path:"/my"});
        
            }
        }
    },
    methods:{
        register(){
            this.$router.push({path:"/register"});
        }
    },
    created(){
        this.$store.commit('user/isquit');
    }
}
</script>
